<template>
  <div id="app">
    <side-bar @search-keyword="handleKeywordSearch" @rank-search="handleRankSearch" />
    <div id="container">
      <div v-if="showKeywordSearch">
        <KeyWord_Search />
      </div>
      <div v-if="showRankSearch">
        <Rank_Search />
      </div>
    </div>
  </div>
</template>


<script>
import SideBar from '@/components/sidebar.vue';
import KeyWord_Search from '@/components/KeyWord_Search.vue';
import Rank_Search from '@/components/Rank_Search.vue';

export default {
  name: "App",
  components: {
    SideBar,
    KeyWord_Search,
    Rank_Search,
  },
  data() {
    return {
      showKeywordSearch: false,
      showRankSearch: false,
    };
  },
  methods: {
    handleKeywordSearch() {
      this.showKeywordSearch = true;
      this.showRankSearch = false;
    },
    handleRankSearch() {
      this.showKeywordSearch = false;
      this.showRankSearch = true;
    },
  },
};
</script>

